import React from 'react';
import './bootstrap.min.css';

class EmotionTable extends React.Component {
  constructor(props) {
    super(props);
    console.log(props);
    let x = "";
    console.log(Object.entries(this.props.emotions.result.emotion.document).map((entry) => x += entry));
  }

  render() {
    return (
      <div>
        {/*You can remove this line and the line below. */}
        {JSON.stringify(this.props.emotions)}
        <br/>
        <br/>
        <table className="table table-bordered">
          <tbody>
            {
              Object.entries(this.props.emotions.result.emotion.document).map(function (mapentry) {
                return (
                  <tr>
                    <td>{mapentry[0]}</td>
                    <td>{mapentry[1]}</td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>
      </div>
    );
  }

}
export default EmotionTable;
